.feed__container {
  background: #1c1c21;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 0px 2px rgba(0, 0, 0, 0.06),
    0px 0px 1px rgba(0, 0, 0, 0.04);
  border-radius: 40px;
  box-sizing: border-box;
  cursor: pointer;
  text-decoration: none;
  color: #f2f2f3;
  height: 246px;
}

.feed__container_small {
  height: 214px;
}
